@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Source+Sans+Pro:wght@300&family=Work+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noticia+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+JP&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0}body{background-color:black}.header{position:fixed;width:100%}.header_bg{object-fit:cover;width:100%}.paper_container{position:relative;top:500px;display:flex;align-items:center;justify-content:center;z-index:100}.paper{padding:1.5rem;width:50rem;text-align:center;background-color:rgba(226,226,226,0.6);color:#2E2D27}.paper h2{margin-top:0.5rem}.paper p{margin:0.5rem;font-size:large}.spread{margin-top:1.5rem;display:flex;justify-content:space-between;align-items:center}.icon-button{float:right;margin-right:1rem;font-size:xx-large}.divide{margin:1rem;display:flex;justify-content:space-between}.divide p{margin-left:1rem}.divide p:first-child{margin-left:0;padding-right:1rem;border-right:2px solid #2E2D27}.poem{width:100%}.poem img{width:100%;object-fit:contain}.pics img{height:25rem;object-fit:contain}p{font-family:'Noto Sans', sans-serif}li{font-family:'Noto Sans', sans-serif}

